import React, { useContext, useRef, useEffect } from "react";
import Chart from "chart.js";
import Container from "react-bootstrap/Container";
import Jumbotron from "react-bootstrap/Jumbotron";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import ListGroup from "react-bootstrap/ListGroup";
import moment from "moment";
import { IncidentsContext } from "../../Context";

export default () => {
    const [incidents] = useContext(IncidentsContext);
    const barChartRef = useRef();
    const lineChartRef = useRef();

    let monthly = [{name:"Jan",out:0},{name:"Feb",out:0},{name:"Mar",out:0},{name:"Apr",out:0},{name:"May",out:0},{name:"Jun",out:0},{name:"Jul",out:0},{name:"Aug",out:0},{name:"Sep",out:0},{name:"Oct",out:0},{name:"Nov",out:0},{name:"Dec",out:0}];

    useEffect(() => {
        const yearAgo = new Date(new Date().setFullYear(new Date().getFullYear() - 1));
        incidents.forEach(incident => {
            if (incident.end) {
                const incidentStart = new Date(Date.parse(incident.start));
                const incidentEnd = new Date(Date.parse(incident.end));
                const incidentOut = Math.floor(incidentEnd-incidentStart);
                
                if (incidentStart > yearAgo) {
                    // const index = incidentStart.getMonth();
                    // const new = monthly[index].out + incidentOut;
                    monthly[incidentStart.getMonth()].out += incidentOut;
                    // monthly[incidentStart.getMonth()] += incidentOut;
                }
            }
        });

        const currentMonth = (new Date()).getMonth();
        
        for (let x=0; x<currentMonth+1; x++) {
            monthly.push(monthly.shift());
        }
        monthly.reverse();

        const barchart = new Chart(barChartRef.current.getContext('2d'), {
            type: "bar",
            data: {
                labels: incidents.slice(0,7).map((item,i) => i+1),
                datasets: [{
                    backgroundColor: "#6c757d",
                    data: incidents.slice(0,7).map(item => calcTime(item.start, item.end))
                }]
            },
            options: {
                legend: {
                    display: false
                },
                scales: {
                    xAxes: [{
                        barPercentage: 0.5,
                        barThickness: 6,
                        maxBarThickness: 8,
                        minBarLength: 2,
                        gridLines: {
                            offsetGridLines: true
                        }
                    }],
                    yAxes: [{
                        ticks: {
                            callback: (value) => {
                                return `${moment.duration(value, "milliseconds").humanize()}`;
                            }
                        }
                    }]
                }
            }
        });
        console.log(barchart)
        const linechart = new Chart(lineChartRef.current.getContext('2d'), {
            type: "line",
            data: {
                labels: monthly.map(item => item.name),
                datasets: [{
                    label: "Year on year",
                    backgroundColor: "#6c757d",
                    data: monthly.map(item => item.out)
                }]
            },
            options: {
                legend: {
                    display: false
                },
                scales: {
                    xAxes: [{
                        barPercentage: 0.5,
                        barThickness: 6,
                        maxBarThickness: 8,
                        minBarLength: 2,
                        gridLines: {
                            offsetGridLines: true
                        }
                    }],
                    yAxes: [{
                        ticks: {
                            callback: (value) => {
                                return `${moment.duration(value, "milliseconds").humanize()}`;
                            }
                        }
                    }]
                }
            }
        });
        console.log(linechart)
    });
    
    const calcTime = (start,end) => {
        const endtime = new Date(end).getTime(), starttime = new Date(start).getTime();
        return Math.floor(endtime - starttime);
    };

    return <React.Fragment>
        <Jumbotron fluid className="mb-5 text-light bg-secondary">
            <Container>
                <h1 className="display-4">Dashboard</h1>
                <p className="lead">/ˈdaʃbɔːd/ 1. A graphical summary of various pieces of important information, typically used to give an overview of a business.</p>
                <hr className="my-4"/>
            </Container>
        </Jumbotron>
        <Container>
            {incidents ? <Row className="mb-5 mt-5">
                <Col lg={8} className="animated zoomIn faster">
                    <div className="animated zoomIn faster"><canvas ref={barChartRef}/></div>
                    <div className="animated zoomIn faster mb-5 mt-5"><canvas ref={lineChartRef}/></div>
                </Col>
                
                <Col lg={4} className="animated zoomIn faster">
                    <ListGroup>
                        {incidents.slice(0,7).map((item,i) => <ListGroup.Item key={i}>
                            <div className="d-flex w-100 justify-content-between">
                                <h6 className="mb-1">{i+1}. {item.application}</h6>
                            </div>
                            <small>{item.startText}</small><br/>
                            <small className="text-muted">{item.outageText}</small>
                        </ListGroup.Item>)}
                    </ListGroup>
                </Col>
                
            </Row> : null}
        </Container>
        
    </React.Fragment>
}